<template>
    <div id="cards" v-if="!loading">
        <el-table :data="Object.keys(stocks)" style="width: 100%">
            <el-table-column type="index" label="編號" width="50"></el-table-column>
            <el-table-column type="data" label="股票代號" width="130">
                <template slot-scope="scope">
                    <a :href="filterPchomeUrl(scope.row)" target="_blank">{{ scope.row }}</a>
                </template>
            </el-table-column>
            <el-table-column type="data" label="名稱" width="130">
                <template slot-scope="scope">
                    <a :href="filterYahooUrl(scope.row)" target="_blank">{{ stocks[scope.row][stocks[scope.row].length-1]['name'] }}</a>
                </template>
            </el-table-column>
            <el-table-column type="data" label="收盤價" width="130">
                <template slot-scope="scope">
                    {{ stocks[scope.row][stocks[scope.row].length-1]['c'] }}
                </template>
            </el-table-column>
            <el-table-column type="data" label="交易量" width="130">
                <template slot-scope="scope">
                    <p>交易量： {{ stocks[scope.row][stocks[scope.row].length-1]['v'] }}</p>
                    <p>平均交易量： {{ stocks[scope.row][stocks[scope.row].length-1]['avgVolume'] }}</p>
                </template>
            </el-table-column>
            <el-table-column type="data" label="黃金分割率" width="130">
                <template slot-scope="scope">
                    <p>超強勢：{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection']['超強勢'] }}</p>
                    <p>強勢：{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection']['強勢'] }}</p>
                    <p>中度：{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection']['中度'] }}</p>
                    <p>弱勢：{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection']['弱勢'] }}</p>
                    <p>超弱勢：{{ stocks[scope.row][stocks[scope.row].length-1]['goldSection']['超弱勢'] }}</p>
                </template>
            </el-table-column>
            <el-table-column label="主力動向">
                <template slot-scope="scope">
                    <p>
                        <a :href="filterMainTrendUrl(scope.row)" target="_blank">前往</a>
                    </p>
                </template>
            </el-table-column>
            <el-table-column label="大戶籌碼">
                <template slot-scope="scope">
                    <p>
                        <a :href="filterMainTrendUrl(scope.row)" target="_blank">前往</a>
                    </p>
                </template>
            </el-table-column>
        </el-table>
        
    </div>
</template>

<style lang="scss">
</style>

<script>
import { Get_Steps_Stock  } from "../../api/api.js";
export default {
    name: "Cards",
    mounted: function () {
        this.getData();
    },
    props:['handleLoading','loading'],
    components: {},
    data() {
        return {
            stocks:null,
        };
    },
    computed: {},
    methods: {
        async getData() {
            try {
                let response = await Get_Steps_Stock();
                this.stocks = response.data;
                console.log(Object.keys(response.data));
                this.handleLoading();
            } catch (error) {
                console.log(error);
            }
        },
        filterMainTrendUrl(code) {
            let str = `https://www.wantgoo.com/stock/${code}/major-investors/main-trend#main-trend`;
            return str;
        },
        filterMajorInvestorsUrl(code){
            let str = `https://www.wantgoo.com/stock/${code}/major-investors/concentration`;
            return str;
        },
        filterPchomeUrl(code) {
            let str = "https://pchome.megatime.com.tw/stock/sto0/ock1/sid" + code + ".html";
            return str;
        },
        filterYahooUrl(code) {
            let str = "https://tw.stock.yahoo.com/q/ta?s=" + code;
            return str;
        },
    },
};
</script>
